function setZero(num) {
  var tempNum = parseInt(num);
  if (tempNum < 10) {
    return '0' + tempNum;
  } else {
    return '' + tempNum;
  }
}

function renderData(templateId, data, $container) {
  var html = template(templateId, data);
  $container.append(html);
}

function scrollX(obj, scrollTo, time) {
  var scrollFrom = parseInt(obj.scrollLeft);
  var i = 0;
  var runEvery = 5;
  scrollTo = parseInt(scrollTo);
  time /= runEvery;
  var interval = setInterval(function () {
    i++;
    obj.scrollLeft = (scrollTo - scrollFrom) / time * i + scrollFrom;
    if (i >= time) {
      clearInterval(interval);
    }
  }, runEvery);
}
