var ActivityMain = function () {
  this.currentIndex = 0;
  this.topNavShow = false;
  this.skuListTop = [];
  this.currentTopIndex = 0;
};

ActivityMain.prototype.renderPage = function () {
  var _self = this;

  var currentTime = globalConfig.Env === undefined ? Date.parse('2016/11/09 10:20:00') : parseInt($('.j_CountDown').attr('data-servertime'));

  $.ajax({
    url: globalConfig.Env === undefined ? './seckill.json' : '/api/shop/119/clockActivity',
    type: 'GET',
    dataType: 'json',
    success: function (result) {
      $('.j_TimeSelectList').empty();

      var buyStartFlag = false;

      var tempSecKillList = result.data.map(function (data, index) {
        data.name = data.name.split('[')[1].split(']')[1];
        data.index = index;
        if (currentTime < $('.j_ActivityStartTime').val()) {
          data.beforeTime = true;
        }

        if (currentTime < Date.parse('2016/11/09 ' + setZero(data.clock) + ':00:00')) {
          $('.j_TimeSelectList').append('<div class="time-block j_TimeBlock" data-index="' + index + '">' +
            setZero(data.clock) + ':00<br/>即将开抢</div>');
          data.current = false;
          data.canBuy = false;
        } else if (currentTime >= Date.parse('2016/11/09 ' + setZero(data.clock) + ':00:00') &&
          currentTime < Date.parse('2016/11/09 ' + setZero(data.clock + 1) + ':00:00')) {
          $('.j_TimeSelectList').append('<div class="time-block active j_TimeBlock" data-index="' + index +
            '">' + setZero(data.clock) + ':00<br/>抢购中</div>');
          _self.currentIndex = index;
          data.current = true;
          buyStartFlag = true;
          if (data.inventory > 0) {
            data.canBuy = true;
          } else {
            data.canBuy = false;
          }
          scrollX($('.j_TimeSelectBlock')[0], (parseInt(index) - 2) * 71, 200);
        } else {
          $('.j_TimeSelectList').append('<div class="time-block j_TimeBlock" data-index="' + index + '">' +
            setZero(data.clock) + ':00<br/>已开抢</div>');
          if (data.inventory > 0) {
            data.canBuy = true;
          } else {
            data.canBuy = false;
          }
          data.current = false;
        }

        data.commission = data.commission.toFixed(2);
        data.price = data.price.toFixed(2);
        data.actPrice = data.actPrice.toFixed(2);
        return data;
      });

      if (!buyStartFlag) {
        tempSecKillList[0].current = true;
        $($('.j_TimeSelectList').find('.j_TimeBlock')[0]).addClass('active');
      }

      renderData('secondKillItemTemplate', {
        data: tempSecKillList
      }, $('.j_SecondKillItemBlock'));

      $('.j_CoverScroll').removeClass('hide');
    }
  });

  $.ajax({
    url: globalConfig.Env === undefined ? './skuList.json' : '/api/shop/119/activity',
    type: 'GET',
    dataType: 'json',
    success: function (result) {
      // console.log(currentTime >= Date.parse('2016/11/09 00:00:00'));
      result.data.forEach(function (data, index) {
        var $itemBlocklist = $('.j_ItemBlockList' + index);
        var tempDataList = data.map(function (value) {
          value.name = value.name.split('[')[1].split(']')[1];
          if (currentTime < Date.parse('2016/11/09 00:00:00')) {
            value.beforeTime = true;
          }
          value.commission = value.commission.toFixed(2);
          if (currentTime >= $('.j_ActivityStartTime').val()) {
            value.canBuy = true;
          } else {
            value.canBuy = false;
          }
          return value;
        });

        renderData('skuListItemTemplate', {
          data: tempDataList
        }, $itemBlocklist.find('.j_ItemList'));

        $('.j_Lazyload').lazyload({
          threshold: 500
        });

        _self.skuListTop.push($itemBlocklist.offset().top - 50);
      });
    }
  });
};

ActivityMain.prototype.countDown = function () {
  var $countDown = $('.j_CountDown');
  var serverTime = globalConfig.Env === undefined ? Date.parse('2016/11/09 00:00:00') : parseInt($countDown.attr('data-servertime'));
  var endTime = globalConfig.Env === undefined ? Date.parse('2016/11/09 23:59:58') : parseInt($countDown.attr('data-endtime'));

  var counter = new $.countdown({
    correctDateOffset: parseInt((serverTime - Date.now()) / 1000),
    endDate: endTime / 1000,
    onUpdate: function (arr) {
      var d = arr.days;
      var h = arr.hours;
      var m = arr.minutes;
      var s = arr.seconds;

      $countDown.find('.j_Hour').text(h + 24 * d);
      $countDown.find('.j_Minute').text(setZero(m));
      $countDown.find('.j_Second').text(setZero(s));
    }
  });
  counter.start();
};

ActivityMain.prototype.secKill = function () {
  var _self = this;
  $('.j_TimeSelectList').on('click', '.j_TimeBlock', function () {
    var $target = $(this);
    var timeIndex = $target.attr('data-index');

    if (_self.currentIndex !== parseInt(timeIndex)) {
      $('.j_TimeSelectList').find('.j_TimeBlock').removeClass('active');
      $target.addClass('active');

      if (timeIndex === '0' || timeIndex === '1' || timeIndex === '2') {
        scrollX($('.j_TimeSelectBlock')[0], 0, 200);
      } else if (timeIndex === '17' || timeIndex === '18' || timeIndex === '19') {
        scrollX($('.j_TimeSelectBlock')[0], 1062, 200);
      } else {
        scrollX($('.j_TimeSelectBlock')[0], (parseInt(timeIndex) - 2) * 71, 200);
      }

      var $currentNode = '';
      var $lastNode = '';

      if (parseInt(timeIndex) < _self.currentIndex) {
        $currentNode = $($('.j_SecondKillItemBlock').find('.j_SecondKillItem')[parseInt(_self.currentIndex)]);
        $lastNode = $($('.j_SecondKillItemBlock').find('.j_SecondKillItem')[parseInt(timeIndex)]);
        $currentNode.addClass('leftRightAnimation');
        $lastNode.removeClass('hide').addClass('rightLeftAnimation');
        setTimeout(function () {
          $currentNode.addClass('hide').removeClass('leftRightAnimation');
          $lastNode.removeClass('rightLeftAnimation');
        }, 300);
      } else {
        $currentNode = $($('.j_SecondKillItemBlock').find('.j_SecondKillItem')[parseInt(_self.currentIndex)]);
        $lastNode = $($('.j_SecondKillItemBlock').find('.j_SecondKillItem')[parseInt(timeIndex)]);
        $currentNode.addClass('rightLeftAnimationOut');
        $lastNode.removeClass('hide').addClass('leftRightAnimationOut');
        setTimeout(function () {
          $currentNode.addClass('hide').removeClass('rightLeftAnimationOut');
          $lastNode.removeClass('leftRightAnimationOut');
        }, 300);
      }

      _self.currentIndex = parseInt(timeIndex);
    }
  });

  $('.j_SecondKillBlock').on('click', '.j_ItemSelectBlock', function () {
    var $target = $(this);
    if ($target.find('.j_SelectBtn').hasClass('selected')) {
      //下架
      $target.find('.j_SelectBtn').removeClass('selected');
      $.ajax({
        url: '/api/shop/shopProduct/unselect',
        type: 'GET',
        data: {
          skuId: $target.parents('.j_SecondKillItem').attr('data-skuid')
        },
        dataType: 'json',
        success: function () {
          // if (result.isSuccess === 1) {
          //   console.log('success');
          // } else {
          //   console.log('failure');
          // }
        }
      });
    } else {
      //上架
      $target.find('.j_SelectBtn').addClass('selected');

      $.ajax({
        url: '/api/shop/shopProduct/select',
        type: 'GET',
        data: {
          skuId: $target.parents('.j_SecondKillItem').attr('data-skuid'),
          type: 3
        },
        dataType: 'json',
        success: function () {
          // if (result.isSuccess === 1) {
          //   console.log('success');
          // } else {
          //   console.log('failure');
          // }
        }
      });
    }
  });

  $('.j_SecondKillItemBlock').on('click', '.j_Buy', function () {
    window.location.href = '/shop/sku/' + $(this).parents('.j_SecondKillItem').attr('data-skuid') + '.html';
  });
};

ActivityMain.prototype.topNav = function () {
  var _self = this;

  $(window).on('scroll', function () {
    var scrollHeight = document.documentElement.scrollTop ?
      document.documentElement.scrollTop : document.body.scrollTop;
    if (scrollHeight < 640) {
      if (_self.topNavShow) {
        _self.topNavShow = false;
        $('.j_TopNavCover').addClass('hideAnimate');
        $('.j_TopNav').addClass('hideAnimate');
        setTimeout(function () {
          $('.j_TopNavCover').removeClass('hideAnimate').addClass('hide');
          $('.j_TopNav').removeClass('hideAnimate').addClass('hide');
        }, 300);
      }
      if (_self.currentTopIndex !== 0) {
        scrollX($('.j_TopNav')[0], 0, 200);
        $('.j_TopNav').find('.j_TopNavNode').removeClass('active');
        $($('.j_TopNavNode')[0]).addClass('active');
        _self.currentTopIndex = 0;
      }
    } else if (scrollHeight >= 640) {
      if (!_self.topNavShow) {
        _self.topNavShow = true;
        $('.j_TopNavCover').removeClass('hide').addClass('showAnimate');
        $('.j_TopNav').removeClass('hide').addClass('showAnimate');
        setTimeout(function () {
          $('.j_TopNavCover').removeClass('showAnimate');
          $('.j_TopNav').removeClass('showAnimate');
        }, 300);
      }

      if (scrollHeight >= _self.skuListTop[0] && scrollHeight < _self.skuListTop[1]) {
        if (_self.currentTopIndex !== 0) {
          scrollX($('.j_TopNav')[0], 0, 200);
          $('.j_TopNav').find('.j_TopNavNode').removeClass('active');
          $($('.j_TopNavNode')[0]).addClass('active');
          _self.currentTopIndex = 0;
        }
      } else if (scrollHeight >= _self.skuListTop[1] && scrollHeight < _self.skuListTop[2]) {
        if (_self.currentTopIndex !== 1) {
          scrollX($('.j_TopNav')[0], 0, 200);
          $('.j_TopNav').find('.j_TopNavNode').removeClass('active');
          $($('.j_TopNavNode')[1]).addClass('active');
          _self.currentTopIndex = 1;
        }
      } else if (scrollHeight >= _self.skuListTop[2] && scrollHeight < _self.skuListTop[3]) {
        if (_self.currentTopIndex !== 2) {
          scrollX($('.j_TopNav')[0], 47, 200);
          $('.j_TopNav').find('.j_TopNavNode').removeClass('active');
          $($('.j_TopNavNode')[2]).addClass('active');
          _self.currentTopIndex = 2;
        }
      } else if (scrollHeight >= _self.skuListTop[3] && scrollHeight < _self.skuListTop[4]) {
        if (_self.currentTopIndex !== 3) {
          scrollX($('.j_TopNav')[0], 94, 200);
          $('.j_TopNav').find('.j_TopNavNode').removeClass('active');
          $($('.j_TopNavNode')[3]).addClass('active');
          _self.currentTopIndex = 3;
        }
      } else if (scrollHeight >= _self.skuListTop[4]) {
        if (_self.currentTopIndex !== 4) {
          scrollX($('.j_TopNav')[0], 94, 200);
          $('.j_TopNav').find('.j_TopNavNode').removeClass('active');
          $($('.j_TopNavNode')[4]).addClass('active');
          _self.currentTopIndex = 4;
        }
      }
    }
  });

  $('.j_TopNavNode').on('click', function () {
    var $target = $(this);
    var areaIndex = $target.attr('data-index');
    var $body = document.getElementsByTagName('body')[0];
    $body.scrollTop = _self.skuListTop[areaIndex] + 20;
    // scrollY($body, _self.skuListTop[areaIndex] + 20, 0);
  });
};

