ActivityMain.prototype.eventBind = function () {
  $('.j_SideNavControl').on('click', function () {
    var $sideNavOperate = $('.j_SideNavOperate');
    if ($sideNavOperate.hasClass('down')) {
      $sideNavOperate.removeClass('down').addClass('up');
      $('.j_SideNavOperateText').text('收起');
      $('.j_SideNav').removeClass('hide').addClass('show');
    } else {
      $sideNavOperate.removeClass('up').addClass('down');
      $('.j_SideNavOperateText').text('展开');
      $('.j_SideNav').removeClass('show').addClass('hide');
    }
  });

  $('.j_SideTopNav').on('click', function () {
    window.location.href = $(this).attr('data-url');
  });

  $('.j_CommonwealItemRedirect').on('click', function () {
    window.location.href = $(this).attr('data-url');
  });

  $('.j_CommonwealRankRedirect').on('click', function () {
    window.location.href = $(this).attr('data-url');
  });

  $(document).on('click', '.j_CantBuy', function () {
    Dialog.toast('11月9号零点准时开抢，请耐心等待哦~');
    // location.href = '/shop/sku/'+ $(this).attr('data-skuid')+ '.html';
  });
};

ActivityMain.prototype.init = function () {
  this.renderPage();
  this.countDown();
  this.secKill();
  this.topNav();
  this.skuList();
  this.eventBind();
  this.pageShare();
};

$(document).ready(function () {
  var currentTime = parseInt($('.j_CountDown').attr('data-servertime'));
  var endTime = parseInt($('.j_CountDown').attr('data-endtime'));

  if (currentTime >= endTime) {
    $('.j_EndCover').removeClass('hide');
  }

  if (!globalConfig.Browser.versions.showjoyShopiOs && !globalConfig.Browser.versions.showjoyShopAndroid) {
    if (location.href.indexOf('?Main=1') > -1) {
      var url = location.href;
      url = url.replace('?Main=1', '');
      location.href = url;
    }
  }
  new ActivityMain().init();
});



